import { useState } from 'react';
import { validateTitle } from '../../../../../utils/validateTitle';
import api from '../../../../../api/request';
import { toast } from 'sonner';

export const useEditableTitle = (initialTitle: string, endpoint: string, onSuccess?: () => void) => {
  const [isEditing, setIsEditing] = useState(false);
  const [editedTitle, setEditedTitle] = useState(initialTitle);
  const [error, setError] = useState<string | null>(null);

  const handleUpdateTitle = async () => {
    if (editedTitle.trim() === initialTitle.trim()) {
      toast.info('    ');
      setIsEditing(false);
      return;
    }

    const { isValid, errorMessage } = validateTitle(editedTitle);
    if (!isValid) {
      setError(errorMessage);
      return;
    }

    try {
      await api.put(endpoint, { title: editedTitle });
      toast.success('  ');
      setIsEditing(false);
      setError(null);
      onSuccess?.();
    } catch {
      toast.error('   ');
      setError('   ');
    }
  };

  return {
    isEditing,
    editedTitle,
    error,
    setIsEditing,
    setEditedTitle,
    setError,
    handleUpdateTitle,
  };
};
